<!-- 支付 -->
<template>

	<view class="content">
		<topBar bg_color="#fff">
			<text class="iconfont icon-back-line" @click="back"></text>
			<text class="topBarText fs18 fw400">支付</text>
			<text></text>
		</topBar>
		<!-- 订单总价： -->
		<view class="TO_price dis_flex aic ju_b">
