		color: #999999;
		line-height: 30upx;
		box-sizing: border-box;
		padding-left: 60upx;
	}

	.price1 {
		font-family: PingFang SC;
		color: #333333;
		line-height: 23upx;
		margin-left: 21upx;
	}

	.icon_img {
		width: 58upx;
		height: 58upx;
	}

	.icon_img1 {
		width: 58upx;
		height: 43upx;
	}

	.icon-weixinzhifu {
		color: #13B657;
	}

	.payBtn {
		width: 694upx;
		height: 80upx;
		background-color: #FF1919;
		border-radius: 10upx;
		line-height: 80upx;
		margin-top: 132upx;
	}

	.radio1 {
		width: 42upx;
		height: 42upx;
		border-radius: 50%;
		background-color: #FF1919;
	}
	.radio2 {
		width: 42upx;
		height: 42upx;
		border: 1px solid #999999;
		border-radius: 50%;
	}

	.rimg {
		width: 26upx;
		height: 26upx;
	}
</style>
